import Vue from 'vue';
import App from './App';
import router from './router';
import './utils/rem';
import store from './store/store';

/* eslint-disable-next-line */
//引入修改后的mint-ui.css
import './style/mint-ui.css';
/* eslint-disable-next-line */
import { Swipe, SwipeItem, Tabbar, TabItem, TabContainer, TabContainerItem ,DatetimePicker,Picker,Popup,Lazyload } from 'mint-ui';
import lazyImage from './components/common/lazyImage';

Vue.config.productionTip = false;
Vue.component('swiper', Swipe);
Vue.component('swiper-item', SwipeItem);
Vue.component(Tabbar.name, Tabbar);
Vue.component(TabItem.name, TabItem);
Vue.component(TabContainer.name, TabContainer);
Vue.component(TabContainerItem.name, TabContainerItem);
Vue.component(DatetimePicker.name, DatetimePicker);
Vue.component(Picker.name, Picker);
Vue.component(Popup.name, Popup);
Vue.use(Lazyload);
Vue.component(lazyImage.name, lazyImage);
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
});




