<template>
  <div id="app">
    <router-view ></router-view>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  mounted() {
    console.log(22222222222);
    console.log(this.$route);
  },
};
</script>

<style lang="less">
body,
div,
span,
header,
footer,
nav,
textarea,
section,
ul,
dl,
dt,
dd,
li,
p,
button,
input,
select {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  font-weight: normal;
  font-family: -apple-system-font, 'Helvetica Neue', sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  &:hover {
    outline: none;
  }
}
html,
body {
  position: absolute;
  width: 100%;
  height: 100%;
}
#app {
  position: absolute;
  width: 7.5rem;
  height: 100%;
  margin: 0 auto;
}
.slide-left-enter,
.slide-right-leave-active {
  /* opacity: 1; */
  z-index: 1000;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  /* opacity: 0; */
  z-index: -1000;
  -webkit-transform: translate(-15px, 0);
  transform: translate(-15px, 0);
  transition: all 0.36s cubic-bezier(0.3, 0.1, 0.32, 1);
}
image[lazy=loading] {
  width: 100%;
  height: 100%;
  margin: auto;
  background-color: goldenrod;
}
</style>



