import Vue from 'vue';
import Router from 'vue-router';
import transition from '@/components/transition';

const ystHome = () =>
  import(/* webpackChunkName: "ystHome" */ '@/pages/ystHome/ystHome');
const ystNewsDetail = () =>
  import(/* webpackChunkName: "ystHome" */ '@/pages/yst_news_detail/yst_news_detail');
const ystLookBaby = () =>
  import(/* webpackChunkName: "ystLookBaby" */ '@/pages/yst_lookBaby/yst_lookBaby');
const ystLookBabyLive = () =>
  import(/* webpackChunkName: "ystHome" */ '@/pages/yst_lookBaby_live/yst_lookBaby_live');
const ystFeedMedicine = () =>
  import(/* webpackChunkName: "yst_feedMedicine" */ '@/pages/yst_feedMedicine/yst_feedMedicine');
const ystClassTable = () =>
  import(/* webpackChunkName: "yst_classTable" */ '@/pages/yst_classTable/yst_classTable');
const ystBabyReport = () =>
  import(/* webpackChunkName: "yst_babyReport" */ '@/pages/yst_babyReport/yst_babyReport');
const ystBabyFood = () =>
  import(/* webpackChunkName: "yst_babyFood" */ '@/pages/yst_babyFood/yst_babyFood');
const ystAskLeave = () =>
  import(/* webpackChunkName: "yst_askLeave" */ '@/pages/yst_askLeave/yst_askLeave');
const growLine = () =>
  import(/* webpackChunkName: "growLine" */ '@/pages/growLine/growLine');
const teachChild = () =>
  import(/* webpackChunkName: "teachChild" */ '@/pages/teachChild/teachChild');
const tecdLive = () =>
  import(/* webpackChunkName: "tecd_live" */ '@/pages/tecd_live/tecd_live');
const tecdSleepStory = () =>
  import(/* webpackChunkName: "tecd_sleepStory" */ '@/pages/tecd_sleepStory/tecd_sleepStory');
const tecdRecommendRead = () =>
  import(/* webpackChunkName: "tecd_recommendRead" */ '@/pages/tecd_recommendRead/tecd_recommendRead');
const message = () =>
  import(/* webpackChunkName: "message" */ '@/pages/message/message');
const msgNotice = () =>
  import(/* webpackChunkName: "msg_notice" */ '@/pages/msg_notice/msg_notice');
const msgSystemNotice = () =>
  import(/* webpackChunkName: "msg_systemNotice" */ '@/pages/msg_systemNotice/msg_systemNotice');
const me = () => import(/* webpackChunkName: "me" */ '@/pages/me/me');
const index = () =>
  import(/* webpackChunkName: "index" */ '@/pages/index/index');
const login = () =>
  import(/* webpackChunkName: "login" */ '@/pages/login/login');

const register = () =>
  import(/* webpackChunkName: "register" */ '@/pages/register/register');
const registerPassword = () =>
  import(/* webpackChunkName: "register_password" */ '@/pages/register_password/register_password');

Router.prototype.goBack = function () {
  // this.isBack = true;
  window.history.go(-1);
};

Vue.use(Router);
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'transition',
      component: transition,
      children: [
        {
          path: '/',
          name: 'index',
          component: index,
        },
        {
          path: '/login',
          name: 'login',
          component: login,
        },
        {
          path: '/register',
          name: 'register',
          component: register,
        },
        {
          path: '/registerPassword',
          name: 'register_password',
          component: registerPassword,
        },
        {
          path: '/ystHome',
          name: 'ystHome',
          component: ystHome,
        },
        {
          path: '/ystNewsDetail',
          name: 'yst_news_detail',
          component: ystNewsDetail,
        },
        {
          path: '/ystLookBaby',
          name: 'yst_lookBaby',
          component: ystLookBaby,
        },
        {
          path: '/ystLookBabyLive',
          name: 'yst_lookBaby_live',
          component: ystLookBabyLive,
        },
        {
          path: '/ystClassTable',
          name: 'yst_classTable',
          component: ystClassTable,
        },
        {
          path: '/ystBabyReport',
          name: 'yst_babyReport',
          component: ystBabyReport,
        },
        {
          path: '/ystBabyFood',
          name: 'yst_babyFood',
          component: ystBabyFood,
        },
        {
          path: '/ystAskLeave',
          name: 'yst_askLeave',
          component: ystAskLeave,
        },
        {
          path: '/ystFeedMedicine',
          name: 'yst_feedMedicine',
          component: ystFeedMedicine,
        },
        {
          path: '/growLine',
          name: 'growLine',
          component: growLine,
        },
        {
          path: '/teachChild',
          name: 'teachChild',
          component: teachChild,
        },
        {
          path: '/tecdLive',
          name: 'tecd_live',
          component: tecdLive,
        },
        {
          path: '/tecdSleepStory',
          name: 'tecd_sleepStory',
          component: tecdSleepStory,
        },
        {
          path: '/tecdRecommendRead',
          name: 'tecd_recommendRead',
          component: tecdRecommendRead,
        },
        {
          path: '/message',
          name: 'message',
          component: message,
        },
        {
          path: '/msgNotice',
          name: 'msg_notice',
          component: msgNotice,
        },
        {
          path: '/msgSystemNotice',
          name: 'msg_systemNotice',
          component: msgSystemNotice,
        },
        {
          path: '/me',
          name: 'me',
          component: me,
        },
      ],
    },
    {
      path: '*',
      redirect: '/',
    },
  ],
});



