<template>
  <div class="image-container">
    <img v-lazy="imgUrl"/>
  </div>
</template>

<script>
  export default {
    name: 'lazy-image',
    props: ['imgUrl'],
  };
</script>

<style scoped>
  .image-container {
    width: 100%;
    height: 100%;
  }
  .image-container  img {
    width: 100%;
    height: 100%;
  }
</style>



