import Vue from 'vue';
import Vuex from 'vuex';
import { SOME_MUTATION, NEW_DEVICE } from './mutation-type';

Vue.use(Vuex);
const store = new Vuex.Store({
  state: {
    count: 0,
    currentDevice: { id: 123456 },
    showTabbar: true,
    pageNum: 0,
    pageTransform: 'slide-right',
    pageList: ['login'],
  },
  getters: {
    deviceId: state => state.currentDevice.id,
  },
  mutations: {
    [SOME_MUTATION](state) {
      console.log(state.device.id);
    },
    [NEW_DEVICE](state, device) {
      state.currentDevice = device;
    },
    pageChange(state, pageName) {
      const num = state.pageList.indexOf(pageName);
      if (num < 0) {
        if (state.pageNum !== 0) {
          state.pageList.length = state.pageNum;
        }
        state.pageList.push(pageName);
        state.pageNum++;
        state.pageTransform = 'slide-left';
      } else if (num === 0) {
        if (state.pageNum !== 0) {
          state.pageTransform = 'slide-right';
        } else {
          state.pageTransform = 'slide-left';
        }
        state.pageNum = 0;
        state.pageList = ['login'];
      } else if (state.pageNum > num) {
        state.pageNum = num;
        state.pageTransform = 'slide-left';
      } else {
        state.pageNum = num;
        state.pageTransform = 'slide-right';
      }
    },
  },
});

export default store;



