<template>
  <div>
  <transition :name="transitionName">
    <router-view class="child-view"></router-view>
  </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      transitionName: 'slide-left'
    };
  },
  beforeRouteUpdate(to, from, next) {
    // const isBack = this.$router.isBack;
    // store.state.old = state.now;
    // store.state.now = now;
    this.$store.commit('pageChange', to.name);
    console.log(this.$store.state.pageList);
    this.transitionName = this.$store.state.pageTransform;

    // !this.$router.isBack;
    next();
  }
};
</script>

<style scoped>
.child-view {
  position: absolute;
  width: 100%;
   height: 100%;
  background: #fff;
  -webkit-overflow-scrolling: touch;
  overflow: scroll;
  /* -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);*/
  transition: all 0.36s cubic-bezier(0.52, 0.1, 0.32, 1);
}
.slide-left-enter,
.slide-right-leave-active {
  /* opacity: 1; */
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  /* opacity: 0; */
  -webkit-transform: translate(-15px, 0);
  transform: translate(-15px, 0);
}
</style>



